import { Component, OnInit } from '@angular/core';
import { VhAlgorithm, VhAuth, VhQueryAffiliater } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';

@Component({
  selector: 'app-manage-product',
  templateUrl: './manage-product.component.html',
  styleUrls: ['./manage-product.component.scss']
})
export class ManageProductComponent implements OnInit {


  public date: Date[] = [new Date(), new Date()];
  pageIndex: number = 0;
  pageSize: number = 20;
  lowValue: number = 0;
  highValue: number = 20;
  pageSizeOption: any = [20, 50, 100, 200, 500];
  data: any = [];

  loading: boolean = false;
  isVisibleEdit = false;
  key_code = '';
  customer: any;
  main_package: any = '';
  affiliate: any;
  selling_price: number = 0;
  constructor(
    private vhQueryAffiliater: VhQueryAffiliater,
    private vhComponent: VhComponent,
    private vhAuth: VhAuth,
    public vhAlgorithm: VhAlgorithm
  ) { }

  ngOnInit(): void {
    this.loading = true;
    this.vhQueryAffiliater.getAffiliate()
      .then((affiliate: any) => {
        console.log('getAffiliate', affiliate);
        this.data = affiliate.payment_details;
        this.affiliate = affiliate;
        this.loading = false;
      })
  }



  handleOk(): void {
    this.vhComponent.alertMessageDesktop("warning","Chưa có hàm từ fw")
    this.isVisibleEdit = false;
    // this.vhComponent.showLoading('');
    // this.vhQueryAffiliater.(this.affiliate)
    //   .then((res: any) => {
    //     this.vhComponent.hideLoading();
    //     this.vhComponent.showToast('success', 'Update Affiliate', 'Affiliate updated successfully');
    //   })
    //   .catch((err: any) => {
    //     this.vhComponent.hideLoading();
    //     this.vhComponent.showToast('danger', 'Update Affiliate', 'Failed to update affiliate');
    //   });
  }

}
